import React, { lazy, Component } from 'react'
import {
    CBadge,
    CButton,
    CButtonGroup,
    CCard,
    CCardBody,
    CCardFooter,
    CCardHeader,
    CCol,
    CProgress,
    CRow,
    CCallout
} from '@coreui/react'

import CIcon from '@coreui/icons-react'
import "grapesjs/src/styles/scss/main.scss";
import 'grapesjs/dist/css/grapes.min.css';

import './New.css'
import grapesjs from 'grapesjs';
import { Editor } from 'grapesjs-react';

import Axios from '../../lib/api';


class New extends Component {
    constructor(props) {
        super(props);
        this.state = {
            title: ''
        }
        this.pageId = props.match.params._id;
    }
    componentDidMount = () => {
        this.editor = grapesjs.init({
            // Indicate where to init the editor. You can also pass an HTMLElement
            container: '#gjs',
            // Get the content for the canvas directly from the element
            // As an alternative we could use: `components: '<h1>Hello World Component!</h1>'`,
            fromElement: true,
            // Size of the editor
            // height: '300px',
            width: 'auto',
            storageManager: {
                id: 'gjs-',             // Prefix identifier that will be used inside storing and loading
                type: 'local',          // Type of the storage
                autosave: true,         // Store data automatically
                autoload: true,         // Autoload stored data on init
                stepsBeforeSave: 1,     // If autosave enabled, indicates how many changes are necessary before store method is triggered
                storeComponents: true,  // Enable/Disable storing of components in JSON format
                storeStyles: true,      // Enable/Disable storing of rules in JSON format
                storeHtml: true,        // Enable/Disable storing of components as HTML string
                storeCss: true,         // Enable/Disable storing of rules as CSS string
            },

            // Avoid any default panel
            panels: {
                defaults: [
                    // ...
                    {
                        id: 'panel-devices',
                        el: '.panel__devices',
                        buttons: [{
                            id: 'device-desktop',
                            // label: 'D',
                            command: 'set-device-desktop',
                            active: true,
                            togglable: false,
                        }, {
                            id: 'device-mobile',
                            // label: 'M',
                            command: 'set-device-mobile',
                            togglable: false,
                        }],
                    }]
            },
            mediaCondition: 'min-width', // default is `max-width`
            deviceManager: {
                devices: [{
                    name: 'Mobile',
                    width: '320',
                    widthMedia: '',
                }, {
                    name: 'Desktop',
                    width: '',
                    widthMedia: '1024',
                }]
            },
            plugins: [
                'grapesjs-lory-slider',
                'grapesjs-tabs',
                'grapesjs-custom-code',
                'grapesjs-touch',
                'grapesjs-parser-postcss',
                'grapesjs-tooltip',
                'grapesjs-tui-image-editor',
                'grapesjs-typed',
                'grapesjs-style-bg',
                'gjs-preset-webpage',
            ],
            pluginsOpts: {
                'grapesjs-lory-slider': {
                    sliderBlock: {
                        category: 'Extra'
                    }
                },
                'grapesjs-tabs': {
                    tabsBlock: {
                        category: 'Extra'
                    }
                },
            }
        });

        // Commands
        this.editor.Commands.add('set-device-desktop', {
            run: editor => editor.setDevice('Desktop')
        });
        this.editor.Commands.add('set-device-mobile', {
            run: editor => editor.setDevice('Mobile')
        });

        if (this.pageId)
            Axios.get(`utility/pages?id=${this.pageId}`).then(r => {
                console.log(r)
                this.setState({ page: r.data[0] })
                this.editor.setHtml(r.data[0].html)
                this.editor.setCss(r.data[0].css)
            })
    }
    onSave() {
        var { title } = this.state;
        Axios.post('utility/add-page', { html: this.editor.getHtml(), css: this.editor.getCss(), title: title })
            .then(r => {
                console.log(r)
                this.props.history.push('/dashboard');
            })
            .catch(e => console.error(e))


    }
    onTitleChange(e) {
        this.setState({ title: e => e.target.value })
    }
    render() {

        return (
            <>
                <CRow>
                    <CCol>
                        <CCard>
                            <CCardHeader>
                                Add New Page

                            </CCardHeader>
                            <CCardBody>
                                <input type="text" placeholder="Title " onChange={(e) => this.onTitleChange(e)} />
                                <CButton type="submit" size="sm" color="primary" onClick={() => { this.onSave() }}><CIcon name="cil-scrubber" /> Save</CButton>

                                {/* <Editor /> */}
                                <div id="gjs">
                                    <h1>Hello World Component!</h1>
                                </div>

                            </CCardBody>
                        </CCard>
                    </CCol>
                </CRow>
            </>
        )
    }
}

export default New
